<template>
  <div>
    <Header name="对公付款"></Header>
    <div class="modify-content">
      <van-form @submit="onSubmit">
        <van-field
          disabled
          v-model="form.titleOfAccount"
          name="账户名称"
          label="账户名称"/>
        <van-field
          disabled
          v-model="form.bankOfDeposit"
          name="开户银行"
          label="开户银行"/>
        <van-field
          disabled
          v-model="form.bankAccount"
          name="银行账户"
          label="银行账户"/>
        <van-field
          disabled
          v-model="form.payAmount"
          name="待付金额"
          label="待付金额"/>
        <van-field
          v-model="form.commitAmount"
          name="付款金额"
          :disabled="freightAmountFlag"
          label="付款金额" type="number"/>
        <van-field
          name="uploadImages"
          label="付款凭证">
          <template #input>
            <van-uploader v-model="uploadImages" @delete="deleteRead" :after-read="afterRead" :max-count="4" />
          </template>
        </van-field>
        <div style="text-align: center;">
          <div style="margin: 30px 0px 20px;">
            <van-button round block type="info" native-type="submit" :disabled="form.paymentVoucher.length === 0">已付款</van-button>
          </div>
          <div class="message">
            <img :src="require('@/assets/image/warn.png')" alt="" />
            <span style="display: contents;">公司后台审核付款凭证和确认到账后会安排发货(最多不超过四张)</span>
          </div>
        </div>
      </van-form>
    </div>
  </div>
</template>
<script>

import * as api from '@/data/api/market/index'
import Header from '@/components/Layout/header/index'
import axios from 'axios';

export default {
  name: 'home',
  components :{
    Header
  },
  data () {
    return {
      baseUrl: process.env.BaseUrl + '/minio/upload',
      // 新增请求参数
      form: {
        titleOfAccount: '衡磊科技集团股份有限公司',
        bankOfDeposit: '交通银行合肥阜阳北路支行',
        bankAccount: '341329000018010023378',
        payAmount: this.$route.query.payAmount,
        commitAmount: '',
        paymentVoucher: []
      },
      uploadImages: [],
      params: {
        payVoucher: []
      },
      isSuccess:false,
      freightAmountFlag: this.$route.query.freightAmountFlag
    }
  },
  mounted () {
    this.params = JSON.parse(this.$route.query.orderInfo)
    this.form.commitAmount = this.$route.query.freightAmountFlag ? this.$route.query.payAmount : ''
    this.params.payType = 3
    this.params.payVoucherList = []
  },
  methods: {
    onSubmit () {
      let me = this
      if(me.form.commitAmount === '' || Number(me.form.commitAmount) === 0){
        me.$notify({
          message: '请填写付款金额且金额不能为0',
          color: '#ad0000',
          background: '#ffe1e1',
        })
        return
      }
      me.params.payVoucher = {
        commitAmount: Number(me.form.commitAmount),
        payVoucher: ''
      }
      me.form.paymentVoucher.forEach((item, index)=>{
        if (index + 1 === me.form.paymentVoucher.length) {
          me.params.payVoucher.payVoucher = me.params.payVoucher.payVoucher + item.payVoucher
        } else {
          me.params.payVoucher.payVoucher = me.params.payVoucher.payVoucher + item.payVoucher + ','
        }
      })
      if (me.form.paymentVoucher.length > 0 && Number(me.form.commitAmount) <= Number(me.form.payAmount)) {
          api.orderPayOrder(me.params).then(res => {
            if (res.code === 200) {
              me.$notify({ type: 'success', message: '下单成功，订单号：' + me.params.orderSn})
              me.$nextTick(()=>{
                me.$router.push({
                  path:'/user/myorders',
                  query: {
                    'toorders':-1,
                  }
                })
              }, 1000)
            } else {
              me.$notify({
                message: res.message,
                color: '#ad0000',
                background: '#ffe1e1',
              })
            }
          }).catch((res) => {
            me.$notify({
              message: res.message,
              color: '#ad0000',
              background: '#ffe1e1',
            })
          }).finally(() => {
          })
      } else {
        me.$notify({
          message: '请上传付款凭证或确认付款金额',
          color: '#ad0000',
          background: '#ffe1e1',
        })
      }
    },
    deleteRead (file) {
      let me = this
      me.form.paymentVoucher.forEach((item,index)=>{
        if(item.payVoucher.indexOf(file.file.name) > 0){
          me.form.paymentVoucher.splice(index,1)
        }
      })
    },
    afterRead (file) {
      let me = this
      file.status = 'uploading';
      file.message = '上传中...';
      // me.form.paymentVoucher = []
      // me.params.payVoucher = []
      var formData = new FormData();
      formData.append('file', file.file);
      const instance=axios.create({ withCredentials: true})
      let config = { headers: { "Content-Type": "multipart/form-data" }};
      instance.post(me.baseUrl, formData, config).then(res => {
        if (res.data.code === 200 ) {
          file.status = '';
          file.message = '';
          me.params.payVoucherList.push({'payVoucher': res.data.data.url})
          me.form.paymentVoucher.push({'payVoucher': res.data.data.url})
        } else {
          file.status = 'failed';
          file.message = '上传失败';
          me.$notify({
            message: res.data.message,
            color: '#ad0000',
            background: '#ffe1e1',
          })
        }
      }).catch((res) => {
        me.$notify({
          message: res.message,
          color: '#ad0000',
          background: '#ffe1e1',
        })
      }).finally(() => {
      })
    }
  }
}
</script>
<style lang="stylus" scoped>
@import '~@/styles/variable.styl';
>>>.van-field__control:disabled{
  color: #000;
  -webkit-text-fill-color: #000;
}
.modify-content {
  padding: 30px 15px;
  >>>.van-field__label {
    width: 4.2em;
    font-size: 14px;
    color: #666666;
  }
  >>>.van-cell {
    padding: 20px 5px;
  }
  >>>.van-field__control {
    font-size: 14px;
    font-weight: bold;
  }
  >>>.van-button--info {
    background-color: #FF4C4C;
    border: 1px solid #FF4C4C;
    border-radius: 4px!important;
    font-size: 17px!important;
    padding: 0 10px!important;
  }
  .message {
    /* display: flex;
    align-items: center; */
    margin-left: -2px;
    img {
      width: 14px;
      height: 14px;
      vertical-align: top;
    }
    span {
      margin: 0px 4px;
      font-size: 12px;
      color: #8D8D8D;
      display: inline-block;
    }
  }
}
</style>



